import React from "react";
import { Icon } from "@iconify-icon/react";

interface HeaderProps {
  toggleMobileNav: () => void;
}

const Header = (props: HeaderProps) => (
  <div className="Header">
    <i className="bi bi-list mobile-nav-toggle d-lg-none"></i>
    <header id="header" className="d-flex flex-column justify-content-center">
      <nav id="navbar" className="navbar nav-menu">
        <ul>
          <li onClick={props.toggleMobileNav}>
            <a href="#hero" className="nav-link scrollto">
              <Icon icon="bx-home" /> <span>Home</span>
            </a>
          </li>
          <li onClick={props.toggleMobileNav}>
            <a href="#about" className="nav-link scrollto">
              <Icon icon="bx-user" /> <span>About</span>
            </a>
          </li>
          <li onClick={props.toggleMobileNav}>
            <a href="#resume" className="nav-link scrollto">
              <Icon icon="bx-file-blank" /> <span>Resume</span>
            </a>
          </li>
          <li onClick={props.toggleMobileNav}>
            <a href="#portfolio" className="nav-link scrollto">
              <Icon icon="bx-book-content" />
              <span>Portfolio</span>
            </a>
          </li>
          <li onClick={props.toggleMobileNav}>
            <a href="#services" className="nav-link scrollto">
              <Icon icon="bx-server" />
              <span>Services</span>
            </a>
          </li>
          <li onClick={props.toggleMobileNav}>
            <a href="#contact" className="nav-link scrollto">
              <Icon icon="bx-envelope" />
              <span>Contact</span>
            </a>
          </li>
        </ul>
      </nav>
    </header>
  </div>
);


export default Header;
