import React from "react";
import AOS from "aos";
import About from "./Components/About/About";
import Skills from "./Components/Skills/Skills";
import Resume from "./Components/Resume/Resume";
import Portfolio from "./Components/Portfolio/Portfolio";
import Services from "./Components/Services/Services";
import Contact from "./Components/Contact/Contact";
import Footer from "./Components/Footer/Footer";
import Hero from "./Components/Hero/Hero";
import Header from "./Components/Header/header";
import { Icon } from "@iconify-icon/react";

import "./assets/css/style.css";
import "iconify-icon";
import "aos/dist/aos.css";
import "swiper/css";
import "swiper/css";
import "remixicon/fonts/remixicon.css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import RenderHTMLString from "./Components/compiler/RenderHTMLString";

const App = () => {
  const [isLoading, setIsLoading] = React.useState(true);
  const [isMobileNavActive, setMobileNavActive] = React.useState(false);
  AOS.init({
    duration: 1000,
    easing: "ease-in-out",
    once: true,
    mirror: false,
  });

  React.useEffect(() => {
    // Simulate loading for demonstration purposes
    setTimeout(() => {
      setIsLoading(false);
    }, 2000);
  }, []);

  const toggleMobileNav = () => {
    setMobileNavActive(!isMobileNavActive);
  };

  let backtoTopStr = `<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-up-short" viewBox="0 0 16 16">
 <path fill-rule="evenodd" d="M8 12a.5.5 0 0 0 .5-.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 .5.5z"/>
</svg>`

  return isLoading ? (
    <div id="preloader"></div>
  ) : (
    <div className={`App ${isMobileNavActive ? 'bi-x mobile-nav-active' : 'bi-list'}`}>
      <i className="bi bi-list mobile-nav-toggle d-lg-none" onClick={toggleMobileNav}>
        <Icon icon="bi-list" />
      </i>
      <Header toggleMobileNav={toggleMobileNav} />
      <Hero />
      <main>
        <About />
        <Skills />
        <Resume />
        <Portfolio />
        <Services />
        <Contact />
      </main>
      <Footer />
      <a href='#hero' className="back-to-top active d-flex align-items-center justify-content-center">
        <i className="bi bi-arrow-up-short">
          <RenderHTMLString htmlString={backtoTopStr} />
        </i>
      </a>
    </div>
  );
}

export default App;
